@import url(https://fonts.googleapis.com/css?family=Work+Sans:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {font-size: 100%;} /*16px*/

body {
  font-family: 'Work Sans', sans-serif;
  font-weight: 700;
  line-height: 1.75;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  line-height: 1.3;
  color: hsl(0, 0%, 99%);
}

h1 {
  margin: 0;
  font-size: 2.488rem;
}

h2 {font-size: 2.074rem;}

h3 {font-size: 1.728rem;}

h4 {font-size: 1.44rem;}

h5 {font-size: 1.2rem;}

h6 {font-size: 1rem;}

p {
  margin: 0;
}

small, .text_small {font-size: 0.833rem;}

a {
  color: hsl(291, 47%, 70%);
  text-decoration: none;
  transition: 0.3s;
}

a:hover {
  color: hsl(291, 47%, 90%);
}

.app {
  padding: 3rem 1rem 1rem;
  min-height: 100vh;
  background: hsl(300, 5%, 10%);
  color: hsl(0, 0%, 70%);
}

.container {
  margin: auto;
  width: 100%;
  max-width: 1000px;
}

main {
  text-align: center;
  display: grid;
}

@media (min-width: 768px) {
  main {
    grid-template-columns: auto auto;
  }
}

.color-name {
    text-align: center;
    white-space: nowrap;
    transition: 0.3s;
}

.color-name:hover,
.color-name.success {
    color: hsl(0, 0%, 99%);
}

.color-name:hover {
    cursor: pointer;
}

.header {
    text-align: center;
}

.title {
    line-height: 1.2;
}

.subtitle {
    margin: 1rem auto 2rem;
    line-height: 1.2;
}

.color-window {
    margin: auto auto 1rem;
    height: 200px;
    width: 100%;
}

.text-block {
    margin: 2rem auto;
}

.text-block p {
    margin-bottom: 1rem;
}
.preview-window {
    padding: 1rem;
    text-align: left;
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
    border: 1px solid hsl(0, 0%, 30%);
}

.preview-window h2,
.preview-window p {
    margin: 0 0 0.5rem;
    color: inherit;
}

.preview-window h2 {
    line-height: 1.2;
}

.preview-window p {
    font-weight: 400;
}
aside {
    margin: 4rem 0;
}

.text-label,
.text-input {
    max-width: 400px;
    display: block;
    text-align: left;
}

.text-label {
    margin-top: 1.5rem;
}

.text-input {
    padding: 0.5rem 0;
    background: transparent;
    width: 100%;
    color: hsl(0, 0%, 99%);
    font-size: 20px;
    font-weight: 700;
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
    border: none;
    border-bottom: 2px solid hsl(0, 0%, 70%);
    outline: none;
}

.text-input:focus {
    background: transparent;
}

.section-wrap {
    margin-top: 3rem;
}

.section-wrap section {
    margin-top: 3rem;
}

@media (min-width: 768px) {
    .section-wrap {
        display: grid;
        grid-template-columns: auto auto;
        grid-gap: 3rem;
        gap: 3rem;
    }

    .section-wrap section {
        margin-top: 0;
    }
}

.description {
    text-align: center;
}
.footer {
    text-align: center;
}
.slider ~ .slider {
   margin-top: 1rem;
}

.slider-label {
  font-weight: 700;
  width: 75px;
  text-align: left;
}

.slider-input {
  margin: 0;
  flex: 1 1;
  max-width: 360px;
}

@media (min-width: 400px) {
  .slider {
      display: flex;
      justify-content: center;
  }

  .slider ~ .slider {
    margin-top: 0.75rem;
  }
}

input[type=range] {
  box-sizing: border-box;
  font-size: 16px;
  line-height: 1;
  height: 1.25em;
  background-color: transparent;
  cursor: pointer;
  -webkit-appearance: none;
  width: 100%;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]::-ms-thumb {
  width: 1.5em;
  height: 1.5em;
  margin-top: 0;
  background-color: hsl(0, 0%, 99%);
  border-radius: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
input[type=range]:hover::-webkit-slider-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:hover::-moz-range-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:hover::-ms-thumb {
  border-color: rgba(255, 255, 255, 0.7);
}
input[type=range]:active::-webkit-slider-thumb {
  border-color: #ffffff;
}
input[type=range]:active::-moz-range-thumb {
  border-color: #ffffff;
}
input[type=range]:active::-ms-thumb {
  border-color: #ffffff;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  cursor: pointer;
  height: .75em;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}
input[type=range]::-moz-range-track {
  width: 100%;
  cursor: pointer;
  height: .75em;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  background-color: transparent;
}
input[type=range]::-ms-track {
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.title {
  margin: 1rem 0;
}

.color-grid {
    margin: auto;
    display: inline-grid;
    grid-template-columns: auto;
}

@media (min-width: 375px) {
    .color-grid {
        min-width: 325px;
    }
}

.color-grid-cell {
    margin-bottom: 3rem;
    display: inline-grid;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.color-grid-text-block {
    display: grid;
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
    grid-gap: 1rem;
    gap: 1rem;
    text-align: left;
}

.color-grid-swatch {
    width: 125px;
    height: 125px;
    place-self: center;
}

@media (max-width: 375px) {
    .color-grid-swatch {
        width: 90px;
        height: 90px;
    }
}
